import { UmbElementMixin } from '@umbraco-cms/backoffice/element-api';
import {
	LitElement,
	css,
	customElement,
	html,
	nothing,
	state,
	when,
} from '@umbraco-cms/backoffice/external/lit';
import { LicenceInfo, LicenceStatusView } from '@jumoo/usync-expansions';
import {
	uSyncLicenceContext,
	USYNC_LICENCE_CONTEXT_TOKEN,
} from '@jumoo/usync-expansions';
import { UmbTextStyles } from '@umbraco-cms/backoffice/style';
import { UUIButtonState } from '@umbraco-cms/backoffice/external/uui';
import {
	UMB_CONFIRM_MODAL,
	UMB_MODAL_MANAGER_CONTEXT,
} from '@umbraco-cms/backoffice/modal';

@customElement('usync-licence-view')
export class uSyncLicenceViewElement extends UmbElementMixin(LitElement) {
	@state()
	saveState?: UUIButtonState;

	#licenceContext?: uSyncLicenceContext;

	constructor() {
		super();

		this.consumeContext(USYNC_LICENCE_CONTEXT_TOKEN, (_context) => {
			if (!_context) return;

			this.#licenceContext = _context;

			_context.refresh();

			this.observe(_context.licence, (_licence) => {
				this.licence = {
					key: _licence?.key ?? '',
					domains: _licence?.domains ?? '',
				};
				this.requestUpdate('licence');
			});

			this.observe(_context.licenceStatus, (_status) => {
				this.licenceStatus = _status;
				this.requestUpdate('licenceStatus');
			});
		});
	}

	connectedCallback(): void {
		super.connectedCallback();
	}

	async #saveLicence() {
		this.saveState = 'waiting';
		await this.#licenceContext?.saveLicence(this.licence);
		this.saveState = 'success';
	}

	#updateDomains(e: Event) {
		const value = (e.target as HTMLInputElement).value;
		this.licence.domains = value;
		this.requestUpdate('licence');
	}

	#updateKey(e: Event) {
		const value = (e.target as HTMLInputElement).value;
		this.licence.key = value;
	}

	@state()
	licence: LicenceInfo = {
		key: 'key',
		domains: 'domains',
	};

	@state()
	licenceStatus: LicenceStatusView | undefined = undefined;

	async #clearLicence() {
		const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
		if (!modalManager) return;

		const confirmModal = modalManager.open(this, UMB_CONFIRM_MODAL, {
			data: {
				headline: this.localize.term('usynccomplete_licenceClearHeading'),
				content: this.localize.term('usynccomplete_licenceClearContent'),
				confirmLabel: this.localize.term('general_clear'),
				color: 'danger',
			},
		});

		confirmModal
			.onSubmit()
			.then(async () => {
				await this.#licenceContext?.saveLicence({ key: '', domains: '' });
				this.requestUpdate('licence');
			})
			.catch(() => {
				// console.log('cancel');
			});
	}

	render() {
		return html`
			<umb-body-layout>
				<div class="layout">
					${this.renderStatus()} ${this.renderDetails()} ${this.renderSubscription()}
					${this.renderExpiration()}
				</div></umb-body-layout
			>
		`;
	}

	renderStatus() {
		return html`
			<usync-licence-status .licenceStatus=${this.licenceStatus}></usync-licence-status>
		`;
	}

	renderDetails() {
		return this.licenceStatus
			? html`
					<uui-box headline="uSync.Complete.Licence">
						<div slot="header-actions">
							<uui-button
								type="button"
								look="outline"
								color="danger"
								label="Clear"
								@click=${this.#clearLicence}></uui-button>
						</div>
						<div class="details">
							<umb-property-layout
								label="Domains"
								alias="domains"
								description="Domains licence applys to">
								<div slot="editor">
									<uui-input
										label="domains"
										@input=${this.#updateDomains}
										.value=${this.licence.domains}
										.readonly=${this.licenceStatus?.licenceType ===
										'Subscription'}></uui-input>
								</div>
							</umb-property-layout>

							<umb-property-layout
								label="Licence Key"
								alias="key"
								description="Key for licence">
								<div slot="editor">
									<uui-input
										label="key"
										@input=${this.#updateKey}
										.value=${this.licence.key}></uui-input>
								</div>
							</umb-property-layout>

							<div class="actions">
								<uui-button
									look="primary"
									color="positive"
									label="Save"
									.state=${this.saveState}
									@click=${this.#saveLicence}></uui-button>
							</div>
						</div>
					</uui-box>
				`
			: nothing;
	}

	renderSubscription() {
		if (this.licenceStatus?.licenceType !== 'Subscription') return nothing;
		if (!this.licenceStatus?.expires) return nothing;
		const expiresDate = new Date(this.licenceStatus.expires);
		if (expiresDate >= new Date('9999-12-30')) return nothing;

		return html`<uui-box headline="uSync.Complete.Subscription">
			<umb-localize key="usynccomplete_licenceSubscriptionInfo"></umb-localize>
			<uui-button
				look="secondary"
				color="positive"
				href="https://jumoo.co.uk/resend?sub=${this.licence.key}"
				target="_blank"
				rel="noopener noreferrer"
				>Request subscription details</uui-button
			>
		</uui-box>`;
	}

	renderExpiration() {
		if (!this.licenceStatus?.expires) return nothing;
		const expiresDate = new Date(this.licenceStatus.expires);
		if (expiresDate >= new Date('9999-12-30')) return nothing;

		const today = new Date();
		const remainingDays = Math.ceil(
			(expiresDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24),
		);
		if (expiresDate < today) {
			return html`<uui-box class="alert alert-danger">
				<umb-localize key="usynccomplete_licenceExpired"></umb-localize>
			</uui-box>`;
		}

		return html`<uui-box
			class="alert ${remainingDays <= 60 && !this.licenceStatus.autoRenews ? 'alert-warning' : 'alert-default'}"
		">
		<div class="alert-content">
			<div>
			<umb-localize
				key="usynccomplete_licenceExpires"
				.args=${[expiresDate.toDateString()]}></umb-localize>
			<umb-localize
				key="usynccomplete_licenceDaysLeft"
				.args=${[remainingDays]}></umb-localize>
				</div>

				<div>
			${when(
				this.licenceStatus.autoRenews,
				() =>
					html`<umb-icon name="icon-check"></umb-icon
						><umb-localize key="usynccomplete_licenceAutoRenews"></umb-localize>`,
				() =>
					html`<umb-icon name="icon-alarm-clock"></umb-icon
						><umb-localize key="usynccomplete_licenceNotAutoRenews"></umb-localize>`,
			)}</div>
			</div>
		</uui-box>`;
	}

	static styles = [
		UmbTextStyles,
		css`
			.layout {
				display: flex;
				flex-direction: column;
				gap: var(--uui-size-space-4);
			}

			uui-input {
				width: 100%;
			}

			.actions {
				padding-left: calc(200px + var(--uui-size-layout-2));
			}

			.alert-content {
				display: flex;
				justify-content: space-between;
			}

			.alert-content > div {
				display: flex;
				gap: 10px;
			}

			.alert-danger {
				background-color: var(--uui-color-danger);
				color: var(--uui-color-danger-contrast);
			}

			.alert-default {
				background-color: var(--uui-color-default);
				color: var(--uui-color-default-contrast);
			}

			.alert-warning {
				background-color: var(--uui-color-warning);
				color: var(--uui-color-warning-contrast);
			}
		`,
	];
}

export default uSyncLicenceViewElement;
