import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import { SyncRestoreRepository } from '@jumoo/usync-expansions';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { UmbContextToken } from '@umbraco-cms/backoffice/context-api';

export class SyncRestoreContext extends UmbControllerBase {
	#respository: SyncRestoreRepository;

	constructor(host: UmbControllerHost) {
		super(host);
		this.#respository = new SyncRestoreRepository(this);
		this.provideContext(USYNC_RESTORE_CONTEXT, this);
	}

	async getRestorePoint(key: string) {
		return await this.#respository.get(key);
	}
}

export default SyncRestoreContext;

export const USYNC_RESTORE_CONTEXT = new UmbContextToken<SyncRestoreContext>(
	'usync-restore-context',
);
