import {
	classMap,
	css,
	customElement,
	html,
	nothing,
	property,
	state,
	when,
} from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { ChangeType, USyncActionView } from '@jumoo/usync-expansions';

@customElement('usync-report-summary-view')
export class uSyncResportSummaryView extends UmbLitElement {
	@state()
	showAll: boolean = false;

	@property({ type: String })
	headline: string = 'Change report';

	@property({ type: Array })
	results?: USyncActionView[];

	/**
	 * @description is this being shown as part of a report (or false as results)
	 */
	@property({ type: Boolean })
	isReport: boolean = true;

	async #showDetail(action: USyncActionView) {
		if (action.change == ChangeType.NO_CHANGE) return;

		// TODO . Open the shared details modal
	}

	private countChanges() {
		let changes: number = 0;
		this.results?.forEach((result) => {
			if (result.change !== ChangeType.NO_CHANGE) {
				changes++;
			}
		});

		return changes;
	}

	render() {
		const changes = this.countChanges();

		return html` ${this.renderChangeSummary(changes)}
			<uui-box .headline=${this.headline}>
				<div slot="header-actions">${this.renderToggle()}</div>
				${when(this.showAll || changes > 0, () => this.renderTable())}
			</uui-box>`;
	}

	renderToggle() {
		return html`
			<uui-toggle
				.label=${this.localize.term('uSync_showAll')}
				labelPosition="left"
				?checked=${this.showAll}
				@change=${() => {
					this.showAll = !this.showAll;
				}}></uui-toggle>
		`;
	}

	renderChangeSummary(changes: number) {
		return this.isReport
			? this.renderReportSummary(changes)
			: this.renderResultSummary(changes);
	}

	renderReportSummary(changes: number) {
		return changes > 0
			? html`<div class="change-msg">
					<h4>
						${this.localize.term('uSyncReport_pendingChanges', [
							changes,
							this.results?.length ?? 0,
						])}
					</h4>
				</div>`
			: html`<div class="change-msg positive">
					<h4>${this.localize.term('uSyncReport_noChanges')}</h4>
					<p>${this.localize.term('uSyncReport_noChangesDescription')}</p>
				</div>`;
	}

	renderResultSummary(changes: number) {
		return changes > 0
			? html`<div class="change-msg">
					<h4>
						${this.localize.term('uSyncReport_resultChanges', [
							changes,
							this.results?.length ?? 0,
						])}
					</h4>
				</div>`
			: html`<div class="change-msg positive">
					<h4>${this.localize.term('uSyncReport_noChanges')}</h4>
					<p>${this.localize.term('uSyncReport_resultNoChange')}</p>
				</div>`;
	}

	renderTable() {
		return html`
			<uui-table> ${this.renderTableHeader()} ${this.renderRows()} </uui-table>
		`;
	}

	renderTableHeader() {
		return html`
			<uui-table-head>
				<uui-table-head-cell class="icon-cell" .noPadding=${true}>
					<!-- <umb-icon name="icon-checkbox-dotted"></umb-icon> -->
				</uui-table-head-cell>
				<uui-table-head-cell style="--uui-table-cell-padding: var(--uui-size-space-2);">
					<umb-localize key="uSync_changeName">Name</umb-localize>
				</uui-table-head-cell>
			</uui-table-head>
		`;
	}

	renderRows() {
		const rowsHtml = this.results?.map((result) => {
			if (!this.showAll && result.change == ChangeType.NO_CHANGE) return nothing;

			const icon =
				result.change == ChangeType.NO_CHANGE
					? 'icon-trafic'
					: result.success
						? 'icon-check color-green'
						: 'icon-wrong color-red';

			return html`
				<uui-table-row
					class=${classMap({ changerow: result.change != ChangeType.NO_CHANGE })}>
					<uui-table-cell class="icon-cell" .noPadding=${true}>
						<umb-icon .name=${icon}></umb-icon>
					</uui-table-cell>
					<uui-table-cell
						@click=${() => this.#showDetail(result)}
						.clipText=${true}
						style="--uui-table-cell-padding: var(--uui-size-space-2);">
						<div>${result.name}</div>
						<div class="item-detail">
							<div>${result.itemType}</div>
							<div>${result.change}</div>
						</div>
					</uui-table-cell>
				</uui-table-row>
			`;
		});

		return html`${rowsHtml}`;
	}

	static styles = css`
		.changerow {
			cursor: pointer;
		}

		.icon-cell {
			width: var(--uui-size-8);
		}

		.item-detail {
			display: flex;
			justify-content: space-between;
			font-size: smaller;
			color: var(--uui-color-disabled-contrast);
		}

		.change-msg {
			padding: var(--uui-size-3);
			margin-bottom: var(--uui-size-3);
		}

		.change-msg.positive {
			background-color: var(--uui-color-positive);
			color: #fff;
			box-shadow: var(--uui-shadow-depth-1);
		}

		.change-msg > * {
			margin: var(--uui-size-4) 0;
		}

		uui-box {
			margin-bottom: var(--uui-size-3);
		}

		uui-table-row:hover {
			background-color: var(--uui-color-disabled);
		}
	`;
}

export default uSyncResportSummaryView;
