import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { ItemsService } from '@jumoo/usync-expansions';
import { tryExecute } from '@umbraco-cms/backoffice/resources';

export class SyncItemRepository extends UmbControllerBase {
	#host: UmbControllerHost;

	constructor(host: UmbControllerHost) {
		super(host);

		this.#host = host;
	}

	/**
	 * @description get the sync item representation of an item
	 * @param entityType the entity type of the item
	 * @param id they "key" for the item
	 * @returns a SyncItem (which as the udi in it.)
	 */
	async GetSyncItem(entityType: string, id: string) {
		return await tryExecute(
			this.#host,
			ItemsService.getSyncItem({
				query: {
					entityType: entityType,
					id: id,
				},
			}),
		);
	}
}
