import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { tryExecute } from '@umbraco-cms/backoffice/resources';
import { RestoreService } from '@jumoo/usync-expansions';

export class SyncRestoreRepository extends UmbControllerBase {
	#host: UmbControllerHost;

	constructor(host: UmbControllerHost) {
		super(host);
		this.#host = host;
	}

	async clean() {
		return (await tryExecute(this.#host, RestoreService.clean())).data;
	}

	async remove(id: string) {
		return (await tryExecute(this.#host, RestoreService.remove({ query: { id } }))).data;
	}

	async getAll() {
		return (await tryExecute(this.#host, RestoreService.getAll())).data;
	}

	async get(key: string) {
		return (
			await tryExecute(this.#host, RestoreService.getRestorePoint({ query: { key } }))
		).data;
	}

	async getReport(id: string) {
		return (await tryExecute(this.#host, RestoreService.getReport({ query: { id } })))
			.data;
	}

	async scan() {
		return (await tryExecute(this.#host, RestoreService.scan())).data;
	}
}

export default SyncRestoreRepository;
