import { LicenceStatusView, USYNC_LICENCE_CONTEXT_TOKEN } from '@jumoo/usync-expansions';
import {
	css,
	customElement,
	html,
	nothing,
	state,
} from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { JumooLicenceStyles } from './styles';

@customElement('usync-licence-notice')
export class uSyncLicenceNoticeElement extends UmbLitElement {
	@state()
	licenceStatus: LicenceStatusView | undefined = undefined;

	constructor() {
		super();

		this.consumeContext(USYNC_LICENCE_CONTEXT_TOKEN, (_context) => {
			if (!_context) return;

			this.observe(_context.licenceStatus, async (_status) => {
				if (!this.licenceStatus) await _context.getLicenceStatus();
				this.licenceStatus = _status;
			});
		});
	}

	render() {
		if (this.licenceStatus == undefined) return nothing;
		if (this.licenceStatus?.status == 'Valid') return nothing;

		const status = this.licenceStatus.status;

		const messageString = `usynccomplete_licence${status}`;
		const className = status.toLowerCase();

		const icon = status === 'Invalid' ? 'icon-wrong' : 'icon-alert';

		return html`<div class="licence-alert ${className}">
			<uui-icon .name=${icon}></uui-icon>
			<umb-localize key="${messageString}"></umb-localize>
		</div>`;
	}

	static styles = [
		JumooLicenceStyles,
		css`
			.licence-alert {
				display: block;
			}
		`,
	];
}

export default uSyncLicenceNoticeElement;
