import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import { SyncItemRepository } from '@jumoo/usync-expansions';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { UmbContextToken } from '@umbraco-cms/backoffice/context-api';

export class SyncItemContext extends UmbControllerBase {
	#repository: SyncItemRepository;

	constructor(host: UmbControllerHost) {
		super(host);

		this.#repository = new SyncItemRepository(this);
		this.provideContext(SYNC_ITEM_CONTEXT_TOKEN, this);
	}

	async getSyncItem(entityType: string, id: string) {
		return (await this.#repository.GetSyncItem(entityType, id)).data;
	}
}

export default SyncItemContext;

export const SYNC_ITEM_CONTEXT_TOKEN = new UmbContextToken<SyncItemContext>(
	'SyncItemContext',
);
