import { JumooQueueListElement } from '@jumoo/processing';
import { css, customElement, html, state } from '@umbraco-cms/backoffice/external/lit';

@customElement('usync-pending-list')
export class uSyncBackgroundPendingListElement extends JumooQueueListElement {
	@state()
	_tableColumns = [
		{ name: 'Note', alias: 'note' },
		{ name: 'Source', alias: 'processor' },
		{ name: 'User', alias: 'user' },
		{ name: 'Scheduled', alias: 'scheduled' },
		{ name: 'Actions', alias: 'actions' },
	];

	renderTable() {
		return !this.queue || this.queue.items.length === 0
			? html`<div class="empty">No items in the queue</div>`
			: html`<jumoo-queue-table
					.items=${this.queue.items}
					.tableColumns=${this._tableColumns}
					@process-queue-item=${this._onProcessQueueItem}
					@view-queue-item=${this._viewQueueItem}></jumoo-queue-table>`;
	}

	_viewQueueItem(event: CustomEvent) {
		console.log('view', event.detail);
	}

	static styles = css`
		.empty {
			display: flex;
			justify-content: center;
			padding: 20px;
		}
	`;
}

export default uSyncBackgroundPendingListElement;

declare global {
	interface HTMLElementTagNameMap {
		'usync-pending-list': uSyncBackgroundPendingListElement;
	}
}
