import { UmbEntryPointOnInit } from '@umbraco-cms/backoffice/extension-api';
import { UMB_AUTH_CONTEXT } from '@umbraco-cms/backoffice/auth';

import './restore/index.js';
import './components/index.js';

import { manifests as localizations } from './lang/manifest';
import { manifests as licenceManifests } from './licence/manifest';
import { manifests as itemManifests } from './items/manifests';
import { manifests as restoreManifests } from './restore/manifest';
import { manifests as backgroundManifests } from './background/manifest';
import { client } from './api/client.gen.js';

export * from './exports.js';

export const onInit: UmbEntryPointOnInit = (_host, extensionRegistry) => {
	extensionRegistry.registerMany([
		...licenceManifests,
		...itemManifests,
		...restoreManifests,
		...localizations,
		...backgroundManifests,
	]);

	// remove the add-ons dashboard workspace, complete is now installed.
	extensionRegistry.exclude('usync.workspace.addons');

	_host.consumeContext(UMB_AUTH_CONTEXT, (_auth) => {
		if (!_auth) return;
		const config = _auth.getOpenApiConfiguration();

		client.setConfig({
			baseUrl: config.base,
			credentials: config.credentials,
		});

		client.interceptors.request.use(async (request, _options) => {
			const token = await _auth.getLatestToken();
			request.headers.set('Authorization', `Bearer ${token}`);
			return request;
		});
	});
};
