import {
	customElement,
	html,
	nothing,
	property,
	state,
} from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { RestorePoint } from '@jumoo/usync-expansions';
import { TimeOptions } from '@jumoo/usync-expansions';
import { UmbTableColumn, UmbTableConfig } from '@umbraco-cms/backoffice/components';

/**
 * @element usync-restore-point-table
 * @property {Array<RestorePoint>} restorePoints - The restore points to display
 * @fires delete-restore-point - When the delete options is selected
 * @fires restore-restore-point - When the restore options is selected
 */
@customElement('usync-restore-point-table')
export class uSyncRestorePointTableElement extends UmbLitElement {
	@property({ type: Array })
	restorePoints?: RestorePoint[];

	@state()
	_tableConfig: UmbTableConfig = {
		allowSelection: false,
		hideIcon: false,
	};

	@state()
	_tableColumns: UmbTableColumn[] = [
		{ name: 'Created', alias: 'created' },
		{ name: 'Name', alias: 'name' },
		{ name: 'Source', alias: 'action' },
		{ name: 'User', alias: 'user' },
		{ name: '', alias: 'actions' },
	];

	@state()
	filter: any;

	#onDelete(restorePoint: RestorePoint) {
		this.dispatchEvent(new CustomEvent('delete-restore-point', { detail: restorePoint }));
	}

	#onRestore(restorePoint: RestorePoint) {
		this.dispatchEvent(
			new CustomEvent('restore-restore-point', { detail: restorePoint }),
		);
	}

	render() {
		if (!this.restorePoints || this.restorePoints.length === 0) return nothing;

		const items = this.restorePoints
			.map((restorePoint) => {
				if (!restorePoint) return undefined;

				return {
					id: restorePoint.key,
					icon: 'icon-pushpin',
					data: [
						{
							columnAlias: 'created',
							value: this.localize.date(restorePoint.created, TimeOptions),
						},
						{ columnAlias: 'name', value: restorePoint.name },
						{ columnAlias: 'action', value: restorePoint.action },
						{ columnAlias: 'user', value: restorePoint.user },
						{ columnAlias: 'actions', value: html`${this.renderActions(restorePoint)}` },
					],
				};
			})
			.filter((item) => item !== undefined);

		return html`<umb-table
			.config=${this._tableConfig}
			.columns=${this._tableColumns}
			.items=${items}
			.filter=${this.filter}></umb-table>`;
	}

	renderActions(restorePoint: RestorePoint) {
		return html` <uui-button
				compact
				label="restore"
				color="positive"
				look="default"
				@click=${() => this.#onRestore(restorePoint)}>
				<uui-icon name="icon-paper-plane"></uui-icon>
			</uui-button>

			<uui-button
				compact
				label="delete"
				color="danger"
				look="default"
				@click=${() => this.#onDelete(restorePoint)}>
				<uui-icon name="icon-trash"></uui-icon>
			</uui-button>`;
	}
}

export default uSyncRestorePointTableElement;
