import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { SyncRestoreWorkspaceContext } from '@jumoo/usync-expansions';
import {
	css,
	customElement,
	html,
	nothing,
	state,
} from '@umbraco-cms/backoffice/external/lit';
import { RestorePoint } from '@jumoo/usync-expansions';
import {
	UMB_CONFIRM_MODAL,
	UMB_MODAL_MANAGER_CONTEXT,
} from '@umbraco-cms/backoffice/modal';
import {
	JUMOO_PROCESSING_DIALOG_MODAL,
	JUMOO_PROCESSING_SIDEBAR_MEDIUM_MODAL,
} from '@jumoo/processing';
import { uSyncRestore } from '@jumoo/usync-expansions';

@customElement('usync-restore-workspace-element')
export class uSyncReatoreWorkspaceElement extends UmbLitElement {
	#context: SyncRestoreWorkspaceContext;

	@state()
	restorePoints?: RestorePoint[] = [];

	constructor() {
		super();
		this.#context = new SyncRestoreWorkspaceContext(this);

		this.observe(this.#context.restorePoints, (restorePoints) => {
			if (!restorePoints) return;
			this.restorePoints = restorePoints;
		});
	}

	async #onScanRestorePoints() {
		const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
		if (!modalManager) return;

		const confirmModal = modalManager.open(this, UMB_CONFIRM_MODAL, {
			data: {
				headline: 'Scan for restore points?',
				content: 'Do you want to scan for new and removed restore points?',
				confirmLabel: 'Scan',
				color: 'positive',
			},
		});

		confirmModal
			.onSubmit()
			.then(async () => {
				this.#context.scan();
			})
			.catch(() => {
				// console.log('cancel');
			});
	}

	async #onCreateRestorePoint() {
		const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
		if (!modalManager) return;

		const createModal = modalManager.open(this, JUMOO_PROCESSING_DIALOG_MODAL, {
			data: {
				processor: uSyncRestore.processing.pipeline,
				strategy: uSyncRestore.processing.strategies.create,
				progressLabel: 'Create',
				options: {
					$type: 'RestorePointProcessingOptions',
					title: 'New restore point',
					source: 'manual',
				},
				headline: 'Create a restore point',
			},
		});

		const data = await createModal.onSubmit().catch(async (_rejected) => {
			await this.#context.refresh();
			return;
		});

		if (!data) return;

		await this.#context.refresh();
	}

	async #onRestore(e: CustomEvent<RestorePoint>) {
		await this.#restore(e.detail);
	}

	async #onRestoreLatest() {
		if (!this.restorePoints) return;

		const latest = this.restorePoints[0];
		if (!latest) return;

		await this.#restore(latest);
	}

	async #restore(restorePoint: RestorePoint) {
		const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
		if (!modalManager) return;

		const createModal = modalManager.open(this, JUMOO_PROCESSING_SIDEBAR_MEDIUM_MODAL, {
			data: {
				processor: uSyncRestore.processing.pipeline,
				strategy: uSyncRestore.processing.strategies.report,
				progressLabel: this.localize.term('uSyncRestore_restoreCheck'),
				debug: false,
				options: {
					$type: 'RestorePointProcessingOptions',
					key: restorePoint.key,
				},
				headline: this.localize.term('uSyncRestore_restoreTitle'),
			},
		});

		if (!createModal) return;

		const data = await createModal.onSubmit().catch(async (_rejected) => {
			await this.#context.refresh();
			return;
		});

		if (!data) return;

		await this.#context.refresh();
	}

	async #onDelete(e: CustomEvent<RestorePoint>) {
		const restorePoint = e.detail;
		console.log('delete restore point', restorePoint);

		const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
		if (!modalManager) return;

		const confirmModal = modalManager.open(this, UMB_CONFIRM_MODAL, {
			data: {
				headline: 'Delete restore point',
				content: `do you want to delete the restore point "${restorePoint.name}"?`,
				confirmLabel: 'Delete',
				color: 'danger',
			},
		});

		confirmModal
			.onSubmit()
			.then(async () => {
				await this.#context.remove(restorePoint.key);
				await this.#context.refresh();
			})
			.catch(() => {
				console.log('cancel');
			});
	}

	render() {
		return html`
			<umb-workspace-editor>
				<div slot="header" class="header">
					<h3>Restore points</h3>
					<small>A place you can come back to.</small>
				</div>
				<div slot="action-menu" class="action-menu">
					<uui-button
						color="default"
						look="outline"
						size="small"
						label="scan"
						@click=${this.#onScanRestorePoints}
						><uui-icon name="icon-axis-rotation"></uui-icon> ${this.localize.term(
							'uSyncRestore_scan',
						)}</uui-button
					>
				</div>
				<umb-body-layout>
					<usync-licence-notice></usync-licence-notice>
					<div class="content">
						<div>
							<uui-button
								color="positive"
								look="outline"
								label="Create"
								@click=${this.#onCreateRestorePoint}
								>${this.localize.term('uSyncRestore_create')}</uui-button
							>
						</div>

						${this.renderTable()}
					</div></umb-body-layout
				></umb-workspace-editor
			>
		`;
	}

	renderIntro() {
		return html`
			<uui-box>
				<div slot="headline">${this.localize.term('uSyncRestore_title')}</div>
				<uui-button
						slot="header-actions"
						color="default"
						look="outline"
						size="small"
						label="scan"
						@click=${this.#onScanRestorePoints}><uui-icon name="icon-axis-rotation"></uui-icon> ${this.localize.term('uSyncRestore_scan')}</uui-button>
				</div>
				<div>
					<umb-localize key="uSyncRestore_intro"></umb-localize>
				</div>
				<div class="create">
				</div>
			</uui-box>
		`;
	}

	renderTable() {
		if (this.restorePoints?.length === 0) return nothing;

		return html`
			<uui-box
				.headline=${this.localize.term('uSyncRestore_restorePoints')}
				style="--uui-box-default-padding: 0;">
				<div slot="header-actions">
					<uui-button
						color="default"
						look="secondary"
						size="small"
						label="scan"
						@click=${this.#onRestoreLatest}
						><uui-icon name="icon-paper-plane"></uui-icon> ${this.localize.term(
							'uSyncRestore_restoreLatest',
						)}</uui-button
					>
				</div>
				<usync-restore-point-table
					.restorePoints=${this.restorePoints}
					@delete-restore-point=${this.#onDelete}
					@restore-restore-point=${this.#onRestore}></usync-restore-point-table>
			</uui-box>
		`;
	}

	static styles = css`
		.create {
			display: flex;
			justify-content: center;
			font-size: 1.5em;
			padding: 20px;
		}

		.header h3 {
			margin: 0;
		}

		.action-menu {
			margin-right: var(--uui-size-space-4);
		}

		.content {
			display: flex;
			flex-direction: column;
			gap: var(--uui-size-space-4);
		}
	`;
}

export default uSyncReatoreWorkspaceElement;
