import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import {
	UMB_WORKSPACE_CONTEXT,
	UmbWorkspaceContext,
} from '@umbraco-cms/backoffice/workspace';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { UmbContextToken } from '@umbraco-cms/backoffice/context-api';
import { UmbObjectState } from '@umbraco-cms/backoffice/observable-api';
import { uSyncRestore } from '@jumoo/usync-expansions';
import { SyncRestoreRepository } from '@jumoo/usync-expansions';
import { RestorePoint } from '@jumoo/usync-expansions';

export class SyncRestoreWorkspaceContext
	extends UmbControllerBase
	implements UmbWorkspaceContext
{
	public readonly workspaceAlias: string = uSyncRestore.workspace;
	#repository: SyncRestoreRepository;

	#restorePoints = new UmbObjectState<RestorePoint[] | undefined>(undefined);
	public readonly restorePoints = this.#restorePoints.asObservable();

	getEntityType() {
		return uSyncRestore.entity;
	}

	constructor(host: UmbControllerHost) {
		super(host);

		this.#repository = new SyncRestoreRepository(this);

		this.provideContext(USYNC_RESTORE_WORKSPACE_CONTEXT, this);
		this.provideContext(UMB_WORKSPACE_CONTEXT, this);

		this.refresh();
	}

	async refresh() {
		const restorePoints = await this.#repository.getAll();
		if (restorePoints) {
			this.#restorePoints.setValue(restorePoints);
		}
	}

	async remove(key: string) {
		// removes the restore point.
		await this.#repository.remove(key);
	}

	async scan() {
		// scans the restore points
		await this.#repository.scan();
		await this.refresh();
	}
}

export default SyncRestoreWorkspaceContext;

export const USYNC_RESTORE_WORKSPACE_CONTEXT =
	new UmbContextToken<SyncRestoreWorkspaceContext>('uSyncRestoreWorkspaceContext');
