import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import { UmbContextToken } from '@umbraco-cms/backoffice/context-api';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { uSyncLicenceRepository } from '@jumoo/usync-expansions';
import { Licence, LicenceInfo, LicenceStatusView } from '@jumoo/usync-expansions';
import { UmbObjectState } from '@umbraco-cms/backoffice/observable-api';
import {
	UMB_NOTIFICATION_CONTEXT,
	UmbNotificationContext,
} from '@umbraco-cms/backoffice/notification';

export class uSyncLicenceContext extends UmbControllerBase {
	#repository: uSyncLicenceRepository;
	#notificationContext?: UmbNotificationContext;

	#licence = new UmbObjectState<Licence | undefined>(undefined);
	public readonly licence = this.#licence.asObservable();

	#licenceStatus = new UmbObjectState<LicenceStatusView | undefined>(undefined);
	public readonly licenceStatus = this.#licenceStatus.asObservable();

	constructor(host: UmbControllerHost) {
		super(host);

		this.consumeContext(UMB_NOTIFICATION_CONTEXT, (_instance) => {
			this.#notificationContext = _instance;
		});

		this.#repository = new uSyncLicenceRepository(this);
		this.provideContext(USYNC_LICENCE_CONTEXT_TOKEN, this);
	}

	async refresh() {
		await this.loadLicence();
		await this.getLicenceStatus();
	}

	async saveLicence(licence: LicenceInfo) {
		const { data } = await this.#repository.saveLicence(licence);

		if (data) {
			await this.loadLicence();
			await this.refresh();

			this.#notificationContext?.peek('positive', {
				data: {
					headline: 'Licence saved',
					message: 'Licence has been saved',
				},
			});
		}
	}

	async loadLicence() {
		const { data } = await this.#repository.getLicence();

		if (data) {
			this.#licence.setValue(data);
		}
	}

	async getLicenceStatus() {
		const { data } = await this.#repository.getLicenceStatus();

		if (data) {
			this.#licenceStatus.setValue(data);
		}
	}
}

export default uSyncLicenceContext;

export const USYNC_LICENCE_CONTEXT_TOKEN = new UmbContextToken<uSyncLicenceContext>(
	'uSyncLicenceContext',
);
