import { UmbElementMixin } from '@umbraco-cms/backoffice/element-api';
import {
	LitElement,
	css,
	customElement,
	html,
	nothing,
	property,
} from '@umbraco-cms/backoffice/external/lit';
import { LicenceStatusView } from '@jumoo/usync-expansions';
import { UmbTextStyles } from '@umbraco-cms/backoffice/style';
import { JumooLicenceStyles } from './styles';

@customElement('usync-licence-status')
export class uSyncLicenceStatusView extends UmbElementMixin(LitElement) {
	@property({ type: Object })
	licenceStatus: LicenceStatusView | undefined = undefined;

	render() {
		if (this.licenceStatus == undefined) return nothing;

		const className = this.licenceStatus?.status.toLocaleLowerCase();
		const icon = this.licenceStatus?.status === 'Valid' ? 'icon-check' : 'icon-alert';

		return html` <div class="licence-alert ${className}">
			<uui-icon .name=${icon}></uui-icon> ${this.licenceStatus?.message ??
			'licenece is valid'}
		</div>`;
	}

	static styles = [
		UmbTextStyles,
		JumooLicenceStyles,
		css`
			:host {
				display: block;
				margin-bottom: 10px;
			}
		`,
	];
}

export default uSyncLicenceStatusView;

declare global {
	interface HtmlElementTagNameMap {
		'usync-licence-status': uSyncLicenceStatusView;
	}
}
