import { UmbDataSourceResponse } from '@umbraco-cms/backoffice/repository';
import {
	Licence,
	LicenceInfo,
	LicenceService,
	LicenceStatusView,
} from '@jumoo/usync-expansions';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { tryExecute } from '@umbraco-cms/backoffice/resources';

export interface uSyncLicenceSource {}

export class uSyncLicenceSource implements uSyncLicenceSource {
	#host: UmbControllerHost;

	constructor(host: UmbControllerHost) {
		this.#host = host;
	}

	async checkLegacy(
		product: string,
		version: string,
	): Promise<UmbDataSourceResponse<string>> {
		return await tryExecute(
			this.#host,
			LicenceService.checkLegacy({ query: { product: product, version: version } }),
		);
	}

	async getLicence(
		product: string,
		version: string,
	): Promise<UmbDataSourceResponse<Licence>> {
		return await tryExecute(
			this.#host,
			LicenceService.getLicence({ query: { product: product, version: version } }),
		);
	}

	async getLicenceInfo(
		product: string,
		version: string,
	): Promise<UmbDataSourceResponse<LicenceInfo>> {
		return await tryExecute(
			this.#host,
			LicenceService.getLicence({ query: { product: product, version: version } }),
		);
	}

	async getLicenceStatus(
		product: string,
		version: string,
	): Promise<UmbDataSourceResponse<LicenceStatusView>> {
		return await tryExecute(
			this.#host,
			LicenceService.getLicenceStatus({ query: { product: product, version: version } }),
		);
	}

	async isLicenced(
		product: string,
		version: string,
	): Promise<UmbDataSourceResponse<boolean>> {
		return await tryExecute(
			this.#host,
			LicenceService.isLicenced({ query: { product: product, version: version } }),
		);
	}

	async saveLicence(licence: LicenceInfo): Promise<UmbDataSourceResponse<boolean>> {
		return await tryExecute(this.#host, LicenceService.saveLicence({ body: licence }));
	}
}
