import { UmbControllerBase } from '@umbraco-cms/backoffice/class-api';
import { UmbControllerHost } from '@umbraco-cms/backoffice/controller-api';
import { uSyncLicenceSource } from '@jumoo/usync-expansions';
import { LicenceInfo } from '@jumoo/usync-expansions';

export class uSyncLicenceRepository extends UmbControllerBase {
	#licenceDataSource: uSyncLicenceSource;

	product: string = 'uSync.Complete';
	version: string = '15.0.0';

	constructor(host: UmbControllerHost) {
		super(host);
		this.#licenceDataSource = new uSyncLicenceSource(this);
	}

	async checkLegacy() {
		return this.#licenceDataSource.checkLegacy(this.product, this.version);
	}

	async getLicence() {
		return this.#licenceDataSource.getLicence(this.product, this.version);
	}

	async getLicenceInfo() {
		return this.#licenceDataSource.getLicenceInfo(this.product, this.version);
	}

	async getLicenceStatus() {
		return this.#licenceDataSource.getLicenceStatus(this.product, this.version);
	}

	async isLicenced() {
		return this.#licenceDataSource.isLicenced(this.product, this.version);
	}

	async saveLicence(licence: LicenceInfo) {
		return this.#licenceDataSource.saveLicence(licence);
	}
}
