import { manifests as localizations } from './lang/manifests.js';

import './components/index.js';
import { UMB_CURRENT_USER_IS_ADMIN_CONDITION_ALIAS } from '@umbraco-cms/backoffice/current-user';

const licenceContext: UmbExtensionManifest = {
	type: 'globalContext',
	alias: 'usync.licence.context',
	name: 'uSync licence context',
	js: () => import('./licence.context.js'),
};

const licenceView: UmbExtensionManifest = {
	type: 'workspaceView',
	alias: 'usync-extensions-licence-view',
	name: 'uSync Licence view',
	js: () => import('./licence.element.js'),
	weight: 100,
	meta: {
		label: 'Licence',
		pathname: 'licence',
		icon: 'icon-certificate',
	},
	conditions: [
		{
			alias: 'Umb.Condition.WorkspaceAlias',
			match: 'usync.workspace',
		},
		{
			alias: UMB_CURRENT_USER_IS_ADMIN_CONDITION_ALIAS,
		},
	],
};

export const manifests = [licenceContext, licenceView, ...localizations];
