import { uSyncRestore } from '@jumoo/usync-expansions';

import { manifests as stepManifests } from './steps/manifest';
import { manifests as localizationManifests } from './lang/manifest';

const workspaceAlias = uSyncRestore.workspace;

const restoreMenuItem: UmbExtensionManifest = {
	type: 'usync-menuItem',
	alias: 'usync.restore.menu.item',
	name: 'restore',
	weight: 10,
	meta: {
		label: 'Restore',
		icon: uSyncRestore.icon,
		entityType: uSyncRestore.element,
		menus: ['usync.menu'],
	},
};

const restoreWorkspaceContext: UmbExtensionManifest = {
	type: 'workspaceContext',
	alias: uSyncRestore.context,
	name: 'uSync Restore',
	js: () => import('./workspace/restore.workspace.context'),
	conditions: [
		{
			alias: 'Umb.Condition.WorkspaceAlias',
			match: workspaceAlias,
		},
	],
};

const restoreWorkspace: UmbExtensionManifest = {
	type: 'workspace',
	alias: workspaceAlias,
	name: 'uSync Restore',
	js: () => import('./workspace/restore.workspace.element'),
	meta: {
		entityType: uSyncRestore.element,
	},
};

export const manifests = [
	restoreMenuItem,
	restoreWorkspaceContext,
	restoreWorkspace,
	...stepManifests,
	...localizationManifests,
];
