import { JumooProcessingPipelineList } from '@jumoo/processing';
import { UmbTableColumn } from '@umbraco-cms/backoffice/components';
import { customElement, html, state } from '@umbraco-cms/backoffice/external/lit';

@customElement('usync-process-list')
export class uSyncProcessListElement extends JumooProcessingPipelineList {
	@state()
	tableColumns: UmbTableColumn[] = [
		{ name: 'Alias', alias: 'alias' },
		// { name: 'Strategy', alias: 'strategy' },
		{ name: 'State', alias: 'state' },
		{ name: 'Action', alias: 'action' },
		{ name: 'Background', alias: 'background' },
		// { name: 'Scheduled', alias: 'scheduled' },
		{ name: 'Actions', alias: 'actions' },
	];

	renderListPipelines() {
		return !this.pipelines || this.pipelines.length === 0
			? html`<div class="emtpy">Nothing with status ${this.status}</div>`
			: html`<jumoo-pipelines-table
					.pipelines=${this.pipelines}
					.hideDelete=${this.hideDelete}
					.tableColumns=${this.tableColumns}
					@view-pipeline=${this.onViewPipeline}
					@delete-pipeline=${this.onDeletePipeline}></jumoo-pipelines-table>`;
	}
}

export default uSyncProcessListElement;
